@import url(https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{--dark: black;--light: white}*{margin:0;padding:0;box-sizing:border-box}*::selection{background:black;background:var(--dark);color:white;color:var(--light)}body{font-family:'Rubik', sans-serif;padding:4vw 0;background:white;background:var(--light);color:black;color:var(--dark)}@media (max-width: 433px){body{padding:0}}.one{width:43.625vw;height:100%;padding-right:3vw;position:fixed;left:0;top:2vw;display:flex;align-items:center;justify-content:flex-end}@media (max-width: 433px){.one{width:100%;height:100vh;position:unset;left:unset;top:unset}}.container{display:flex;justify-content:flex-end}@media (max-width: 433px){.container{justify-content:flex-start;flex-direction:column}}.two{width:55vw;height:120vw;padding-left:3vw;border-left:0.3vw solid black;border-left:0.3vw solid var(--dark)}@media (max-width: 433px){.two{width:100%;height:unset;border:none;margin-bottom:31vw}}.smile{width:2vw;fill:black;fill:var(--dark)}@media (max-width: 433px){.smile{width:6vw}}.email{width:2vw;fill:black;fill:var(--dark)}@media (max-width: 433px){.email{width:6vw}}.map{width:2vw;fill:black;fill:var(--dark)}@media (max-width: 433px){.map{width:6vw}}.telegramm{width:2vw;fill:black;fill:var(--dark)}@media (max-width: 433px){.telegramm{width:6vw}}.change-theme-toggle{position:fixed;cursor:pointer;top:2vw;right:2vw}@media (max-width: 433px){.change-theme-toggle{top:5vw;right:5vw;position:absolute}}.change-theme-toggle .moon{height:2.5vw;fill:black;fill:var(--dark)}@media (max-width: 433px){.change-theme-toggle .moon{height:7.5vw}}.change-theme-toggle .sun{height:3vw;fill:black;fill:var(--dark)}@media (max-width: 433px){.change-theme-toggle .sun{height:8vw}}.change-language-toggle{position:fixed;top:2vw;left:2vw;cursor:pointer;font-size:1.5vw}@media (max-width: 433px){.change-language-toggle{position:absolute;top:5vw;left:5vw;font-size:8vw}}

main{display:flex;align-items:flex-end;justify-content:center;flex-direction:column;height:100%;width:30vw}@media (max-width: 433px){main{width:100%;margin-top:9vw}}main h1{font-size:5.561vw;margin-bottom:2vw;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;text-align:right;font-weight:800}@media (max-width: 433px){main h1{font-size:13.561vw;margin-bottom:10vw;position:-webkit-sticky;position:sticky}}main h5{font-size:1.8vw;margin-bottom:8vw;font-weight:500;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;position:relative;background:var(--dark);color:var(--light);padding:0 0.6vw}main h5::selection{background:var(--light);color:var(--dark)}@media (max-width: 433px){main h5{font-size:5.8vw;margin-bottom:24vw;width:unset}}main h5:after{content:'';width:3vw;height:0.3vw;background:var(--dark);position:absolute;top:8vw;border-radius:5vw;right:0}@media (max-width: 433px){main h5:after{width:15vw;height:0.7vw;top:25vw}}a{color:var(--dark)}

.contacts-item{display:flex;align-items:center;justify-content:flex-end;width:18vw;margin:1.042vw 0}@media (max-width: 433px){.contacts-item{justify-content:flex-end;width:100%;margin:1.7vw 0}}.contacts-item p{text-align:end;font-size:1.4vw;margin-right:1vw}@media (max-width: 433px){.contacts-item p{font-size:4.4vw;margin-right:5vw}}@media (max-width: 433px){.contacts-item .icon{display:flex;align-items:center;justify-content:center}}

.text-block{padding-top:2vw;margin-bottom:5vw}@media (max-width: 433px){.text-block{padding-top:15vw;margin-bottom:15vw}}.text-block h1{position:relative;text-transform:uppercase;font-weight:800;font-size:2.778vw;margin-bottom:3vw;background:var(--dark);width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;color:var(--light);padding:0 0.6vw}.text-block h1::selection{background:var(--light);color:var(--dark)}@media (max-width: 433px){.text-block h1{font-size:7.778vw;margin-bottom:9vw}}.text-block h1:after{content:'';width:3vw;height:0.3vw;background:var(--dark);position:absolute;top:4.2vw;border-radius:5vw;left:0}@media (max-width: 433px){.text-block h1:after{width:14vw;height:0.7vw;top:12.2vw}}.text-block p{width:42.361vw;margin:1vw 0;font-size:1.4vw}@media (max-width: 433px){.text-block p{width:88vw;margin:5vw 0;font-size:4.4vw;line-height:6vw}}.text-block h5{list-style:circle}.text-block a{color:var(--dark);font-size:1.5vw}@media (max-width: 433px){.text-block a{font-size:7vw}}.text-block h3{font-size:1.5vw}@media (max-width: 433px){.text-block h3{font-size:5.5vw;line-height:9vw;margin-top:8vw}}

